/*---------------------- STYLES INIT----------------------*/
:root {
  --c-primary: var(--e-global-color-primary);
  --c-secondary: var(--e-global-color-secondary);
  --c-grey: var(--e-global-color-text);
  --c-accent: var(--e-global-color-accent);
  --c-orange: var(--e-global-color-31c33f9);
  --c-dark: var(--e-global-color-c75e4e6);
  --c-light: var(--e-global-color-d5daea7);
  --c-light-gray: var(--e-global-color-2817fd4);
  --c-blue-sky: var(--e-global-color-11d843b);
  --font-size: 16px;
  --font-family: "Open Sans", sans-serif;
}

.etnos-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 998;
}
.etnos-header .etnos-header__mega-menu {
  display: none;
  background-color: #fff;
  position: fixed;
  inset: 0;
  max-height: 100vh;
  padding: 0 30px 0 15px;
}
@media screen and (max-width: 767px) {
  .etnos-header .etnos-header__mega-menu {
    padding-right: 0;
  }
}
.etnos-header.open-menu {
  bottom: 0;
  max-height: 100vh;
  padding-right: 15px;
}
.etnos-header.open-menu .etnos-header__main-hamburger span {
  display: none;
}
.etnos-header.open-menu .etnos-header__main-hamburger span:last-child {
  display: flex;
}
.etnos-header.open-menu .etnos-header__mega-menu {
  display: block;
  padding-top: 106px;
}
.etnos-header__notification {
  z-index: 2;
  margin: 10px 0;
}
.etnos-header__notification .etnos-header__notification-wrap {
  display: flex;
  align-items: center;
  padding: 14px 20px;
  gap: 15px;
  background-color: var(--e-global-color-11d843b);
  color: var(--e-global-color-primary);
}
.etnos-header__notification .etnos-header__notification-wrap h4,
.etnos-header__notification .etnos-header__notification-wrap h3 {
  margin: 0;
}
.etnos-header__notification .etnos-header__notification-wrap i {
  flex: 0 0 30px;
  font-size: 24px;
  text-align: center;
}
.etnos-header__notification .etnos-header__notification-wrap button {
  padding: 8px 20px;
  align-self: flex-start;
  font-size: 24px;
  cursor: pointer;
  border: none;
  transform: translate(20px, -14px);
  background-color: transparent;
}
.etnos-header__notification .etnos-header__notification-wrap button:hover {
  color: var(--e-global-color-c75e4e6);
  background: transparent;
}
.etnos-header__notification .etnos-header__notification-wrap a {
  font-size: 16px;
  font-weight: 400;
  text-decoration: underline;
}
.etnos-header__notification-des {
  flex: 1;
}
.etnos-header ul {
  margin: 0;
}
.etnos-header ul li {
  margin: 0;
  list-style: none;
}
.etnos-header__wrap {
  background-color: #fff;
  display: flex;
  flex-direction: column;
}
.etnos-header__top {
  z-index: 2;
}
.etnos-header__top-wrap {
  display: flex;
  align-items: center;
  height: 42px;
  background-color: #fff;
}
@media screen and (max-width: 767px) {
  .etnos-header__top-wrap {
    gap: 20px;
  }
}
.etnos-header span {
  font-size: 14px;
  font-weight: 700;
  color: var(--e-global-color-primary);
}
.etnos-header a {
  font-size: 14px;
  font-weight: 700;
}
.etnos-header a:hover {
  text-decoration: underline;
}
.etnos-header__notify {
  flex: 1;
}
@media screen and (max-width: 767px) {
  .etnos-header__notify {
    flex: none;
  }
}
.etnos-header__login {
  display: flex;
  align-items: center;
  font-size: 14px;
}
.etnos-header__login i {
  margin-right: 5px;
  color: var(--e-global-color-31c33f9);
}
@media screen and (max-width: 767px) {
  .etnos-header__login i {
    display: none;
  }
}
.etnos-header__dropdown {
  position: relative;
}
@media screen and (max-width: 767px) {
  .etnos-header__dropdown {
    display: none;
  }
}
.etnos-header__dropdown.active .etnos-header__dropdown-wrap {
  display: block;
  opacity: 1;
  visibility: visible;
  right: 0;
  transition: opacity 400ms ease-out, right 300ms;
}
.etnos-header__dropdown.active i {
  transform: scale(-1);
}
.etnos-header__dropdown > span {
  padding-left: 15px;
  margin-left: 15px;
  border-left: 1px solid var(--e-global-color-31c33f9);
  cursor: pointer;
  line-height: 20px;
}
.etnos-header__dropdown-wrap {
  position: absolute;
  right: -50px;
  top: calc(100% + 5px);
  width: 350px;
  background-color: #fff;
  box-shadow: 2px 2px 5px rgba(100, 100, 100, 0.15);
  opacity: 0;
  visibility: hidden;
  transition: opacity 200ms ease-out, right 100ms;
}
.etnos-header__dropdown-wrap a {
  padding: 15px 30px;
  display: block;
}
.etnos-header__dropdown-wrap a:hover {
  background-color: #F1F9FF;
  text-decoration: underline;
  outline: none;
}
.etnos-header__dropdown i {
  margin-left: 5px;
  color: var(--e-global-color-primary);
  font-size: 14px;
  font-weight: 700;
}
.etnos-header__main {
  z-index: 1;
}
@media screen and (max-width: 767px) {
  .etnos-header__main {
    background-color: var(--e-global-color-accent);
  }
}
.etnos-header__main-wrap {
  background-color: var(--e-global-color-accent);
  display: flex;
  align-items: center;
  gap: 30px;
  height: 64px;
}
@media screen and (max-width: 575px) {
  .etnos-header__main-wrap {
    gap: 20px;
  }
}
.etnos-header__main-logo {
  flex-shrink: 0;
  height: 64px;
}
.etnos-header__main-menu {
  flex-shrink: 0;
}
@media screen and (max-width: 767px) {
  .etnos-header__main-menu {
    display: none;
  }
}
.etnos-header__main-menu .menu {
  display: flex;
  align-items: center;
  gap: 30px;
}
.etnos-header__main-menu .menu a:hover {
  text-decoration: none;
}
.etnos-header__main .etnos-header__main-search {
  position: relative;
  flex-grow: 1;
}
@media screen and (min-width: 768px) {
  .etnos-header__main .etnos-header__main-search {
    max-width: 345px;
  }
}
.etnos-header__main .etnos-header__main-search button {
  position: absolute;
  inset: 2px auto 0 0;
  background-color: transparent;
  width: 32px;
  border: none;
  cursor: pointer;
  z-index: 1;
  display: grid;
  place-content: center;
}
.etnos-header__main .etnos-header__main-search input {
  padding-left: 40px;
  height: 32px;
}
.etnos-header__main .etnos-header__main-search i {
  color: var(--e-global-color-primary);
}
.etnos-header__main-hamburger {
  flex-grow: 1;
  display: flex;
  justify-content: flex-end;
  margin-right: 30px;
}
@media screen and (max-width: 767px) {
  .etnos-header__main-hamburger {
    flex-grow: 0;
  }
}
@media screen and (max-width: 575px) {
  .etnos-header__main-hamburger {
    margin-right: 0px;
  }
}
.etnos-header__main-hamburger span {
  cursor: pointer;
  text-transform: uppercase;
  color: var(--e-global-color-primary);
  align-items: center;
  gap: 10px;
  font-size: 18px;
  font-weight: 600;
  display: none;
}
@media screen and (max-width: 767px) {
  .etnos-header__main-hamburger span {
    font-size: 0;
    gap: 0;
    width: 22px;
  }
  .etnos-header__main-hamburger span i {
    font-size: 26px;
  }
}
.etnos-header__main-hamburger span:first-child {
  display: flex;
}

/*-------------------------------------------------*/
/*------------------ FOOTER -------------------*/
:root {
  --c-primary: var(--e-global-color-primary);
  --c-secondary: var(--e-global-color-secondary);
  --c-grey: var(--e-global-color-text);
  --c-accent: var(--e-global-color-accent);
  --c-orange: var(--e-global-color-31c33f9);
  --c-dark: var(--e-global-color-c75e4e6);
  --c-light: var(--e-global-color-d5daea7);
  --c-light-gray: var(--e-global-color-2817fd4);
  --c-blue-sky: var(--e-global-color-11d843b);
  --font-size: 16px;
  --font-family: "Open Sans", sans-serif;
}

.etnos-footer {
  text-align: center;
  padding: 40px 0;
}
.etnos-footer ul,
.etnos-footer li {
  list-style: none;
  margin: 0;
}
.etnos-footer .etnos-footer__top {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 30px;
  background-color: var(--e-global-color-primary);
  padding: 10px 15px;
  margin-bottom: 30px;
}
@media screen and (max-width: 767px) {
  .etnos-footer .etnos-footer__top {
    flex-direction: column;
  }
}
.etnos-footer .etnos-footer__top h3 {
  color: #fff;
  font-weight: 400;
}
.etnos-footer .etnos-footer__top a {
  color: #fff;
  font-size: 21px;
}
.etnos-footer .etnos-footer__top a:hover {
  filter: brightness(2);
}
.etnos-footer .etnos-footer__top-header {
  position: relative;
  padding: 13px 30px 13px 0;
}
@media screen and (max-width: 767px) {
  .etnos-footer .etnos-footer__top-header {
    padding: 0 0 15px;
  }
}
.etnos-footer .etnos-footer__top-header::after {
  content: "";
  display: inline-block;
  position: absolute;
  top: 0;
  bottom: 0;
  right: -2px;
  width: 4px;
  background-color: #ef8200;
}
@media screen and (max-width: 767px) {
  .etnos-footer .etnos-footer__top-header::after {
    width: 50px;
    height: 4px;
    inset: auto auto -4px 50%;
    transform: translateX(-50%);
  }
}
.etnos-footer .etnos-footer__top-social {
  display: flex;
  gap: 25px;
}
.etnos-footer__content-logo {
  text-align: left;
  height: 64px;
}
@media screen and (min-width: 768px) {
  .etnos-footer__content-mobile-menu {
    display: none;
  }
}
.etnos-footer__content-mobile-menu a {
  font-size: 16px;
  font-weight: 700;
}
.etnos-footer__content-wrap {
  margin: 10px 0 30px;
  display: flex;
  justify-content: space-between;
  gap: 30px;
  text-align: left;
}
@media screen and (max-width: 767px) {
  .etnos-footer__content-wrap {
    margin: 15px 0 20px;
    flex-direction: column;
    gap: 15px;
  }
}
.etnos-footer__content-wrap > div {
  flex: 1;
  font-size: 12px;
  line-height: 20px;
  color: var(--e-global-color-primary);
}
.etnos-footer__content .etnos-footer__content-about {
  flex: 0 1 445px;
}
@media screen and (max-width: 991px) {
  .etnos-footer__content .etnos-footer__content-about {
    flex: 0 1 270px;
  }
}
@media screen and (max-width: 767px) {
  .etnos-footer__content .etnos-footer__content-about {
    display: none;
  }
}
.etnos-footer__content .etnos-footer__content-websites {
  flex: 0 1 285px;
}
@media screen and (max-width: 1199px) {
  .etnos-footer__content .etnos-footer__content-websites {
    flex: 1;
  }
}
@media screen and (max-width: 991px) {
  .etnos-footer__content .etnos-footer__content-websites {
    flex: 0 1 150px;
  }
}
@media screen and (max-width: 767px) {
  .etnos-footer__content .etnos-footer__content-websites {
    display: none;
  }
}
.etnos-footer__content h6 {
  margin-bottom: 5px;
}
.etnos-footer__menu {
  padding: 30px 0;
  margin-bottom: 45px;
  border-width: 1px 0;
  border-style: solid;
  border-color: #CBCBCB;
}
@media screen and (max-width: 767px) {
  .etnos-footer__menu {
    display: none;
  }
}
.etnos-footer__menu .menu {
  display: flex;
  justify-content: space-between;
  text-align: left;
}
@media screen and (max-width: 991px) {
  .etnos-footer__menu .menu {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 15px 30px;
  }
}
.etnos-footer__menu .menu .menu-item-has-children > a {
  font-size: 14px;
  font-weight: 700;
}
.etnos-footer__menu .menu a {
  font-size: 12px;
}
.etnos-footer__bottom {
  display: flex;
  gap: 30px;
}
@media screen and (max-width: 767px) {
  .etnos-footer__bottom {
    flex-direction: column-reverse;
    gap: 15px;
  }
}
.etnos-footer__bottom > * {
  flex: 1;
  font-size: 12px;
  line-height: 20px;
}
.etnos-footer__bottom > *:first-child {
  flex: 0 1 445px;
}
@media screen and (max-width: 767px) {
  .etnos-footer__bottom > *:first-child {
    flex: 0 1 100%;
  }
}
.etnos-footer__bottom > *:last-child {
  flex: 0 1 285px;
}
@media screen and (max-width: 1199px) {
  .etnos-footer__bottom > *:last-child {
    flex: 1;
  }
}
@media screen and (max-width: 991px) {
  .etnos-footer__bottom > *:last-child {
    flex: 0 1 150px;
  }
}
@media screen and (max-width: 767px) {
  .etnos-footer__bottom > *:last-child {
    flex: 0 1 100%;
  }
}
.etnos-footer__bottom-social {
  display: flex;
  gap: 15px;
}
.etnos-footer__bottom-social a {
  font-size: 18px;
}
.etnos-footer__bottom .etnos-footer__bottom-copyright {
  text-align: left;
}
@media screen and (max-width: 991px) {
  .etnos-footer__bottom .etnos-footer__bottom-copyright {
    flex: 0 1 270px;
  }
}
@media screen and (max-width: 767px) {
  .etnos-footer__bottom .etnos-footer__bottom-copyright {
    flex: 1 1 100%;
  }
}
.etnos-footer__bottom .menu {
  display: flex;
  gap: 10px;
}
@media screen and (max-width: 767px) {
  .etnos-footer__bottom .menu {
    display: none;
  }
}
.etnos-footer__bottom .menu a {
  font-size: 1em;
  color: var(--e-global-color-secondary);
  text-decoration: underline;
}
.etnos-footer__bottom .menu a:hover {
  color: var(--e-global-color-primary);
}

.etnos-header__mega-menu-wrap {
  scrollbar-width: none;
  overflow-y: auto;
  height: 100%;
  width: 100%;
  max-width: 1110px;
  margin: 0 auto;
  padding: 30px 60px 30px 0;
}
.etnos-header__mega-menu-wrap li {
  line-height: 20px;
}
.etnos-header__mega-menu-wrap #menu-mega-menu {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 60px;
}
.etnos-header__mega-menu-wrap #menu-mega-menu > li {
  flex: 0 1 calc(33.3% - 40px);
}
.etnos-header__mega-menu-wrap #menu-mega-menu a {
  font-size: 16px;
  line-height: 20px;
  font-weight: 700;
  display: inline-block;
}
.etnos-header__mega-menu-wrap #menu-mega-menu a:hover {
  text-decoration: none;
}
.etnos-header__mega-menu-wrap #menu-mega-menu a:not([href]) {
  font-size: 24px;
  line-height: 30px;
  border-bottom: 1px solid var(--e-global-color-primary);
  padding: 0 0 8px;
  margin: 0 0 12px;
  font-weight: 300;
  display: block;
}
.etnos-header__mega-menu-wrap #menu-mega-menu a:not([href]):hover {
  color: currentColor !important;
}
.etnos-header__mega-menu-wrap #menu-mega-menu .sub-menu {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.etnos-header__mega-menu-wrap #menu-mega-menu .sub-menu .sub-menu {
  margin-top: 8px;
}
.etnos-header__mega-menu-wrap #menu-mega-menu .sub-menu .sub-menu a {
  margin-left: 10px;
  font-weight: 400;
}

.etnos-main-wrapper {
  padding-top: 125px;
}
.etnos-main-wrapper.etnos-notification-active {
  padding-top: 225px;
}

span.body-overlay {
  opacity: 0;
  visibility: hidden;
  transition: 0.35s;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  pointer-events: none;
  z-index: 9990;
}

@media screen and (max-width: 767px) {
  .sidebar-open {
    position: relative;
  }
  .sidebar-open span.body-overlay {
    opacity: 1;
    visibility: visible;
  }
}
.etnos-preloader {
  background-color: var(--c-light);
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 9999;
  display: none;
}

@media (max-width: 575px) {
  .size a {
    width: 100%;
  }
  .size a img {
    height: auto !important;
    object-fit: contain;
  }
}